$font-family: "Gilroy", sans-serif;
$color-brand: #ff4419;
$color-brand-2:#FF7400;

a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
li,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
     font-family: "Gilroy", sans-serif !important;
}

//Botones
@import "./botones.scss";

// Men√∫  //
.menu-top {
     border-bottom: 1px solid $color-brand;
     .logo-yaguar {
          max-height: 51px;
          width: auto;
          @media (max-width: 991px) {
               max-height: 40px;
               padding: 5px;
          }
     }
     .nav-link {
          font-family: $font-family;
          font-size: 14px;
          font-weight: medium;
     }
     &-collapse {
          &.navbar-collapse {
               flex-grow: unset;
          }
     }
     .collapse-hidden {
          @media (max-width: 991px) {
               &_pc {
                    display: block !important;
               }
               &_movil {
                    display: none !important;
               }
          }
          @media (min-width: 992px) {
               &_pc {
                    display: none !important;
               }
               &_movil {
                    display: flex !important;
               }
          }
     }
}

header {
     .py-header {
          padding: 100px 0;
          @media (max-width: 991px) {
               padding: 80px 0 40px 0;
          }
     }
     .header {
          &-titulo {
               font-size: 60px;
               font-weight: bold;
               line-height: 68px;
               font-family: $font-family !important;
               text-align: left;
          }
          &-descripcion {
               font-size: 20px;
               font-family: $font-family;
          }
          &-btn {
               width: 198px;
               font-family: $font-family;
               font-size: 16px;
          }
          &-imagen {
               width: 100%;
               &-hidden_pc {
                    display: none;
               }
               &-hidden_movil {
                    display: block;
               }
          }

          @media (max-width: 991px) {
               &-titulo {
                    font-size: 36px;
                    font-weight: bold;
                    line-height: 46px;
                    font-family: $font-family !important;
                    text-align: center;
               }
               &-descripcion {
                    font-size: 18px;
                    font-family: $font-family;
                    text-align: center;
               }
               &-btn {
                    width: 100%;
                    font-family: $font-family;
                    font-size: 18px;
               }
               &-imagen {
                    width: 98%;
                    &-hidden_movil {
                         display: none;
                    }
                    &-hidden_pc {
                         display: block;
                         padding: 25px 0;
                    }
               }
          }
     }
}

.subtitle {
     line-height: 50px;
     font-size: 46px;
     @media (max-width: 991px) {
     }
}

.elegirnos {
     @media (max-width: 991px) {
          .subtitle {
               font-size: 30px;
               line-height: 36px;
               width: 80%;
               .logotipo {
                    height: 30px;
               }
          }
     }
}

.container-beneficio {
     &-uno {
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          grid-template-rows: repeat(3, auto);
          border-radius: 10px;
          .left{
               grid-area: 2 / 1 / 3 / 3;
               z-index: 1;
               padding: 0 30px;
               .title{
                font-size: 32px;
                color: $color-brand-2;
                font-weight: bold;
               }
               .description{
                font-size: 20px;
                font-weight: medium;
               }
          }
          .right {
               grid-area: 1 / 3 / 4 / 5;
               z-index: 0;
               display: flex;
               flex-direction: column;
               margin-top: 50px;
               align-items: flex-start;
               .img_recordatorio, .img_reservado{
                height: 80px;
               }
          }
          .img-fondo_pc{
            grid-area: 1 / 1 / 4 / 5;
            justify-self: end;
            height: 100%;
          }
     }
}

//Footer
footer {
     width: 100%;
     border-top: 1px solid $color-brand;
     padding-bottom: 20px;
     .footer {
          display: flex;
          justify-content: space-between;
          flex-direction: row;
          ul {
               display: flex;
               flex-direction: column;
          }
          li {
               list-style: none;
          }
     }
     @media (max-width: 991px) {
          .footer {
               justify-content: end;
               flex-direction: column;
               ul {
                    display: flex;
                    flex-direction: row;
               }
               li {
                    list-style: none;
               }
          }
     }
}
